  import uniNumberBox from '@/colorui/components/uni-number-box.vue'

  export default {
    components: {
      uniNumberBox
    },
    data() {
      return {
        list: [],
				tags:[],
        tabCur: 0,
				listData:[[]]
      };
    },
    computed: {
      total() {
        let data = this.listData[0].map(res => res.num)
        let num = 0
        data.forEach(res => {
          num += res
        })
        return num
      }
    },
    mounted() {
      this.onGetOpenid()
      this.init()
    },
    methods: {
      init() {
        uni.showLoading({
          title: '加载中...',
          mask: true
        });

        wx.cloud.callFunction({
          name: 'getList',
          data: {},
          success: res => {
						uni.hideLoading()
            console.log(res)
            this.listData = res.result.data.map(res => res.foods)
            this.list = this.listData[0]
            this.tags = res.result.data
          },
          fail: err => {
						uni.hideLoading()
            console.error('[云函数] [getList] 调用失败', err)
          }
        })
      },
      onGetOpenid() {
        // 调用云函数
        wx.cloud.callFunction({
          name: 'login',
          data: {},
          success: res => {
            console.log('[云函数] [login] user openid: ', res.result.openid)
            wx.globalData.openid = res.result.openid
          },
          fail: err => {
            console.error('[云函数] [login] 调用失败', err)
          }
        })
      },
      submit() {
        let data = this.listData[0].filter(res => res.num)
        let str = ''
        data.forEach(res => {
          str += `${res.name}${res.isHot?`(${res.isHot})`:''}*${res.num};\n`
        })
        uni.showModal({
          title: '提示',
          content: `你点了${str}`,
          success:(res) => {
            uni.showLoading({mask:true})
            if (res.confirm) {
              wx.cloud.callFunction({
                name: 'submit',
                data:{list:data},
                success: res => {
                  uni.hideLoading()
                  uni.showToast({
                    title: '成功',
                    icon:'success',
                    duration: 1500
                  })
                },
                fail: err => {
                  uni.hideLoading()
                  console.error('[云函数] [getList] 调用失败', err)
                }
              })
            }
          }
        });
      },
      radioChange(item, la) {
        this.$nextTick(()=>{
          item.isHot = la
        })
			},
      TabSelect(e) {
        this.tabCur = e.currentTarget.dataset.id;
        this.list = this.listData[this.tabCur]
      },
      VerticalMain(e) {
      }
    },
  }
